/** Supplies the component "My posts" with data and function "addPost"*/
import React from 'react';
import {actions} from '../../../redux/profile-reducer';
import MyPosts, { DispatchPropsType, MapsPropsType } from './MyPosts';
import {connect} from 'react-redux';
import { AppStateType } from '../../../redux/redux-store';


let mapStateToProps = (state: AppStateType)=>{
	return {
		postsData : state.profilePage.postsData,
		profilePhoto : state.profilePage.profile?.photos,
		fullName : state.profilePage.profile?.fullName,
	}
};


/** Provide the component ProfileContainer with data and functions */
// Types for "connect"  :  TStateProps = {}, TDispatchProps = {}, TOwnProps = {}, State = DefaultState
const MyPostsContainer = connect<MapsPropsType, DispatchPropsType, {}, AppStateType>(mapStateToProps, 
																												{addPost: actions.addPostActionCreator})(MyPosts)

export default MyPostsContainer;