/** Display "My posts" chapter for profilePage content*/
import React from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';
import AddPostForm from './AddPostForm/AddPostForm';
import { PhotosType, PostType } from '../../../types/types';


export type MapsPropsType = {  
	postsData : Array<PostType>,
	profilePhoto: PhotosType | undefined,
	fullName: string | undefined
}
export type DispatchPropsType = {  
	addPost: (newPostText: string) => void
}


/** Display "My posts" chapter for profilePage content*/
const MyPosts : React.FC<MapsPropsType & DispatchPropsType> = (props => {

	let postsElement = [...props.postsData]
							.reverse()							//New "Posts"are the first to publish
							.map(p => <Post key ={p.id} message={p.message} likesCount={p.likesCount} 
												profilePhoto={props.profilePhoto} fullName={props.fullName}/>);
	return (
		<div className={s.myPosts}>
			<div className={s.addMyPosts}>	
				<h3>My posts:</h3>
				<AddPostForm addPost={props.addPost}/>
			</div>
			
			<div className={s.posts}>
				{postsElement}
			</div>
		</div>
	);
});

/** "memo" is a higher-order component (HOC) that wraps a component and re-renders it only when its props have changed*/
const MyPostsMemo = React.memo(MyPosts)

export default MyPostsMemo;