/** Form in "My Profile" (editing my profile data)*/
import React from 'react';
import { ContactsType, ProfileType } from '../../../types/types';
import { Button, Checkbox, Form, FormProps, Input } from 'antd';


// Structure: 2. profileInfo supply (1. ProfileDataForm) with: "saveProfile"-function-callback

/** What data does the form collect (what the user enters)*/
interface IProfileDataEditFormType {
	fullName: string
	lookingForAJob: boolean | null
	lookingForAJobDescription: string
	aboutMe: string
	contacts: ContactsType
}


/** 1. Main component with a Form*/
export default function ProfileDataForm({ initialValues, saveProfile }: ProfileDataFormPropsType) {
	// const ProfileDataForm = ({handleSubmit, profile, error}) =>{

	/**  "useForm" Generik  What you can do with a form and Default form field values
	 * {register - registration, handleSubmit - data collection in the form, formState- form state (errors here)}*/

	//Successful Submit
	const submitSuccess:  FormProps<IProfileDataEditFormType>['onFinish'] = (formData) => {
		let updatedProfile: ProfileType = {
			...initialValues, fullName: formData.fullName,
			lookingForAJob: formData.lookingForAJob ?? false,
			lookingForAJobDescription: formData.lookingForAJobDescription,
			aboutMe: formData.aboutMe,
			contacts: formData.contacts
		}
		saveProfile(updatedProfile);
	}

	//Unsuccessful Submit
	const errorSubmit:FormProps<IProfileDataEditFormType>['onFinishFailed'] = (errorInfo) => {
	}
	//----------------------------------------------------------------

	return (
		<Form style={{ maxWidth: 600 }}  labelCol={{ span: 8 }}	wrapperCol={{ span: 16 }} initialValues={initialValues}
				onFinish={submitSuccess}
				onFinishFailed={errorSubmit}>

			<Form.Item<IProfileDataEditFormType>
			label="Full name"
			name="fullName"
			rules={[{ required: true, message: 'Please input your username!' },
						{ max: 30, message: 'Full name exceeds 30 symbols' },
						{ min: 2, message: 'Full name  shorter 2 symbols' },
			]}
			>
				<Input />
			</Form.Item>

			<Form.Item<IProfileDataEditFormType>
			label="Looking for a job"
			name="lookingForAJob"
			valuePropName="checked"
			>
				<Checkbox></Checkbox>
			</Form.Item>

			<Form.Item<IProfileDataEditFormType>
			label="My professional skills"
			name="lookingForAJobDescription"
			>
				<Input />
			</Form.Item>
			
			<Form.Item<IProfileDataEditFormType>
			label="About me"
			name="aboutMe"
			>
				<Input />
			</Form.Item>
	

			<Form.Item<IProfileDataEditFormType>
			label="Contacts:"
			>
				<br />
				<Form.Item
				label="GitHub"	name={['contacts', 'github']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }}  
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
								message: 'Incorrect URL format!' },
						]}
				>
				<Input />
				</Form.Item>

				<Form.Item
				label="facebook" name={['contacts', 'facebook']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }} 
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>

				
				<Form.Item
				label="instagram"
				name={['contacts', 'instagram']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }}
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>

				<Form.Item
				label="vk"
				name={['contacts', 'vk']}  labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }} initialValue={initialValues.contacts.vk}
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>

				<Form.Item
				label="X (twitter)"
				name={['contacts', 'twitter']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }} initialValue={initialValues.contacts.website}
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>

				<Form.Item
				label="website"
				name={['contacts', 'website']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }} initialValue={initialValues.contacts.youtube}
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>

				<Form.Item
				label="youtube"
				name={['contacts', 'youtube']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }}
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>

				<Form.Item
				label="main Link"
				name={['contacts', 'mainLink']} labelCol={{ span: 5 }}	wrapperCol={{ span: 19 }}
				rules={[{ pattern: new RegExp(/((http|ftp|https):\/\/){0,1}([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:\/~+#-]*[\w@?^=%&\/~+#-])/), 
					message: 'Incorrect URL format!' },
				]}
				>
				<Input />
				</Form.Item>
			</Form.Item>

			<Form.Item wrapperCol={{ offset: 15, span: 9 }}>
				<Button type="primary" htmlType="submit">
					Save
				</Button>
			</Form.Item>
		</Form>
	)
}

type ProfileDataFormPropsType = {
	initialValues: ProfileType
	saveProfile: (formData: ProfileType) => void
}