import React, { FC, useEffect } from 'react';
import s from './Users.module.css';

import Paginator from '../common/Paginator/Paginator';
import User from './User';
import UserSearchForm from './UserSearchForm';
import { FilterType, requestUsers, follow, unfollow } from '../../redux/users-reducer';
import { useDispatch, useSelector } from 'react-redux';
import { getCurrentPage, getFollowingInProgress, getPageSize, getTotalUsersCount, getUsers, getUsersFilter } from '../../redux/users-selectors';
import { AppDispatch } from '../../redux/redux-store';
import { useNavigate, useSearchParams, useLocation, useParams } from 'react-router-dom';
import { Content } from 'antd/es/layout/layout';
import { Col, ConfigProvider, Pagination, Row } from 'antd';
/** */


export const Users: FC = () => {

	/**"useSelector( our selector)" to get data from state	 */
	const users = useSelector(getUsers)
	const totalUsersCount = useSelector(getTotalUsersCount)
	const pageSize = useSelector(getPageSize)
	const currentPage = useSelector(getCurrentPage)
	const filter = useSelector(getUsersFilter)
	const followingInProgress = useSelector(getFollowingInProgress)

	//TYPE CAST for "UseDispatch" due to Redux changes---
	//const dispatch = useDispatch() that's how it was
	/** "useDispatch( our thunk/action)" to dispatch in state	*/
	const dispatch: AppDispatch = useDispatch()



	useEffect(() => {
		/** At the first load we take data from the "URL" */
		let term=searchParams.get('term');
		let friend=searchParams.get('friend');
		let page=searchParams.get('page');

		let actualPage = currentPage
      let actualFilter = filter

      if (!!page) actualPage = Number(page)


      if (!!term) actualFilter = {...actualFilter, term: term as string}

      switch(friend) {
            case "null":
               actualFilter = {...actualFilter, friend: null}
               break;
            case "true":
               actualFilter = {...actualFilter, friend: true}
               break;
            case "false":
               actualFilter = {...actualFilter, friend: false}
               break;
      }

		/** Request of "User"*/
		dispatch(requestUsers(actualPage, pageSize, actualFilter))
	}, [])


	let navigate = useNavigate();
	// let params = useParams(); //Creates an Object from a "URL"
	//We do synchronization when "filter" arrives. 
	//The "URL" string is formed from: "filter.term", "filter.friend", "currentPage"

	useEffect(() => {
		navigate(`/users?term=${filter.term}&friend=${filter.friend}&page=${currentPage}`);

	}, [navigate, filter.friend, filter.term, currentPage])




	//When loading, it receives an initial set of "Users"
	//"useEffect" - synchronize
	//instead of "ComponentDidMount" use "useEffect"
	
	let location = useLocation();
	const [searchParams] = useSearchParams(location.search);

	

	const onPageChanged = (numberPage: number) => {
		//"requestUsers" - it is a Thunk-Creator( subspecies  ActionCreator-а). Его нельзяя просто вызывать, только "dispatch"-ить
		dispatch(requestUsers(numberPage, pageSize, filter));
	}

	const onFilterChanged = (filter: FilterType) => {
		//Вызывает ф-цию запроса"User"-ов и изменения "filter"-а
		dispatch(requestUsers(1, pageSize, filter)); 	//"currentPage" =1 если фильтр поменялся
	}

	const UI_follow = (userId: number) => {
		dispatch(follow(userId));
	}

	const UI_unfollow = (userId: number) => {
		dispatch(unfollow(userId));
	}
	//------------------------------------------------------------------------------------------------------------------------

	const contentStyle: React.CSSProperties = {
		textAlign: 'center',
		minHeight: 120,
		// lineHeight: '120px',
		// backgroundColor: '#fff',
		borderRadius: 13,
		padding: '30px 24px',
	};

	return (
		<div>
			<Content style={contentStyle}>
				<div className={s.wrapper}>

					<div className={s.UserSearchForm}>
						<UserSearchForm onFilterChanged={onFilterChanged} />
					</div>

					<div>
						<ConfigProvider
						theme={{
							components: {
								Pagination: {
									itemActiveBg: '#e2e4f9',
								},
							}
						}}
						>
							<div className={s.pagination}>
								<Pagination defaultCurrent={currentPage} 
												total={totalUsersCount}
												pageSize={pageSize} 
												showSizeChanger={false}
												onChange={onPageChanged}/>
							</div>
						</ConfigProvider>
					</div>


					<div className={s.usersList}>
						{users.map(u => <User user={u}
													followingInProgress={followingInProgress}
													key={u.id}
													follow={UI_follow}
													unfollow={UI_unfollow}/>
							)
						}
					</div>
				</div>
			</ Content>
		</div>
	);
};
