import React from 'react';
import { LaptopOutlined, NotificationOutlined, UserOutlined, TeamOutlined, SmileOutlined } from '@ant-design/icons';
import { Layout, Menu, theme } from 'antd';
import Preloader from './components/common/Preloader/Preloader';
import { Routes, Route } from 'react-router-dom';
import ProfileContainer from './components/Profile/ProfileContainer';
import DialogsContainer from './components/Dialogs/DialogsContainer';
import UsersPage from './components/Users/UsersContainer';
import LoginPage from './components/Login/Login';
import Music from './components/Music/Music';
import News from './components/News/News';
import Settings from './components/Settings/Settings';
import { Error404 } from './Error404';
import AppHeader from './components/Header/AppHeader';
import  ChatPage  from './pages/Chat/ChatPage';
import SubMenu from 'antd/es/menu/SubMenu';
import { NavLink } from "react-router-dom";


const { Content, Sider } = Layout;


/** Layout for project and
 * all Routes
 */
const AppAntD: React.FC = () => {
  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();

  return (
    <Layout>
      <AppHeader />
      
      <Layout>
        <Sider width={200} style={{ background: colorBgContainer }}>
          <Menu
            mode="inline"
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            style={{ height: '100%', borderRight: 0, padding: '30px 0 0 0' }}
          >

            <SubMenu key="sub1" icon={<UserOutlined/>} title="My Profile">
                <Menu.Item key="1"> <NavLink to="/profile">Profile</NavLink></Menu.Item>
                <Menu.Item key="2"> <NavLink to="/dialogs">Messages</NavLink></Menu.Item>
            </SubMenu>

            {/* <SubMenu key="sub2" icon={<LaptopOutlined/>} title="Users"> */}
                <Menu.Item key="3"  icon={<TeamOutlined/>} ><NavLink to="/users">Users</NavLink></Menu.Item>
            {/* </SubMenu> */}
            <SubMenu key="sub3" icon={<SmileOutlined/>} title="Music">
                <Menu.Item key="4"><NavLink to="/music">List of songs</NavLink></Menu.Item>
            </SubMenu>
            <SubMenu key="sub4" icon={<LaptopOutlined/>} title="Settings">
                <Menu.Item key="5"><NavLink to="/settings">Setting 1</NavLink></Menu.Item>
                <Menu.Item key="6"><NavLink to="/settings">Setting 2</NavLink></Menu.Item>
            </SubMenu>
              <Menu.Item key="9" icon={<NotificationOutlined/>}><NavLink to="/chat">Chat</NavLink></Menu.Item>
          </Menu>
        </Sider>


        <Layout style={{  }}>
          <Content
            style={{
              padding: 24,
              margin: 0,
              minHeight: 280,
              background: "f8f9fa",
              borderRadius: borderRadiusLG,
            }}
          >

            <React.Suspense fallback={<div><Preloader /></div>}>
              <Routes >
                <Route path='/' element={<ProfileContainer />} />
                <Route path='/Profile/:userId?' element={<ProfileContainer />} />
                <Route path='/Dialogs/*' element={<DialogsContainer />} />
                <Route path="/users" element={<UsersPage />} />
                <Route path="/login" element={<LoginPage />} />
                <Route path="/music" element={<Music />} />
                <Route path="/news" element={<News />} />
                <Route path="/settings" element={<Settings />} />
                <Route path="/chat" element={<ChatPage />} />
                <Route path="*" element={<Error404 />} />
              </Routes>
            </React.Suspense>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
};

export default AppAntD;