/** Container for supplying the "Profile" component with data */
import React from 'react';
import Profile from './Profile';
import {connect} from 'react-redux';
import {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile} from '../../redux/profile-reducer'
import { compose } from 'redux';
import { NavigateFunction, useLocation, useNavigate, useParams  } from "react-router-dom";
import { useEffect } from "react";
import { AppStateType } from '../../redux/redux-store.js';
import { ProfileType } from '../../types/types';
/** */

/** Structure: (2 compose (1 ProfileContainer))
 * 2 compose supply (1 ProfileContainer)) with: 	
 * from "State" : 			profile, status, authorizedUserId, isAuth
 * functions-callbacks:	"{getUserProfile, getStatus, updateStatus, savePhoto, saveProfile}"
 * navigation:					"withRouter-ом"
*/

type MapPropsType = ReturnType<typeof mapStateToProps> 

type DispatchPropsType = {
	getUserProfile: (userId: number)=>void
	getStatus: (userId: number)=>void
	updateStatus: (status: string)=>void
	savePhoto: (file : File)=>void
	saveProfile: (profile : ProfileType)=>Promise<any>
}

//Type for WithRouter
type PathParamsType = {
	userId: string,
	navigate: NavigateFunction
}


type PropsType = MapPropsType & DispatchPropsType & PathParamsType

/** Container for supplying the "Profile" component with data */
class ProfileContainer extends React.Component<PropsType>{
	refreshProfile(){
		/**  match from withRouter*/
		let userId: number | null = +this.props.userId;
		/** if there is no "userId"*/
		if (!userId){
			/** then "userId" from authorization */
			userId=this.props.authorizedUserId;
			/** if there is no "userId" in the authorization then log in*/
			if (!userId){
				this.props.navigate("/Login")
			}
		}

		if (!userId){
			console.error("UserId should exist")
		} else{
		/** Request to get a profile of some User*/
		this.props.getUserProfile(userId);
		}
	}


	componentDidMount(){
		this.refreshProfile()
	}

	componentDidUpdate(prevProps: PropsType, prevState: PropsType){
		/** if new params came*/
		if (this.props.userId !== prevProps.userId){
			this.refreshProfile()
		}
	}
	render(){
		return (
			<div>
				{/* If there is no userId, then this is our profile and I am the owner */}
				<Profile {...this.props} 				
				isOwner = {!this.props.userId} 
				profile = {this.props.profile} 
				status = {this.props.status} 
				updateStatus = {this.props.updateStatus}
				savePhoto = {this.props.savePhoto}
				saveProfile ={this.props.saveProfile}
				/>
			</div>
		);
	}
}


let mapStateToProps = (state: AppStateType)=>({
	profile : state.profilePage.profile,
	status : state.profilePage.status,
	authorizedUserId : state.auth.userId,
	isAuth : state.auth.isAuth
	
});

/** A function that creates an wrapping component*/
function withRouter<WCP extends MapPropsType&DispatchPropsType> (WrappedComponent: React.ComponentType<WCP>) { //"WrappedComponent" - обораЧИВАЕМАЯ к-та
	
	//wrapping component supplies with: "location", "navigate", "params"  + "userId ", "navigate"
	function ComponentWithRouterProp(props: MapPropsType & DispatchPropsType) {
		let location = useLocation();
		let navigate = useNavigate();
		let params = useParams(); //Creates an Object from a "URL"


		useEffect(() => {
		if (!props.isAuth) {
			navigate("/login");
		}
		}, [props.isAuth, navigate]);

		//Extracts "userId" from "URL"
		let userId = params.userId;

		return (
			<WrappedComponent
			{...props as WCP}
			router={{ location, navigate, params }} 
			userId = {userId}
			navigate = {navigate}
			/>
		);
	}
	return ComponentWithRouterProp;
	}

/** Provide the component ProfileContainer with data and functions */
export default compose<React.ComponentType>( //compose applies consistently HOCs to component
	connect(mapStateToProps, {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile}),
	withRouter,
)(ProfileContainer);