/** Header of the site and the check if you are logged*/
import React from 'react';
import s from './AppHeader.module.css';
import { NavLink } from 'react-router-dom';
import { Avatar, Button, Col, Layout, MenuProps, Row} from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { useDispatch, useSelector } from 'react-redux';
import { selectCurrentUserLogin, selectIsAuth } from '../../redux/auth-selectors';
import { logout } from '../../redux/auth-reducer';
import { AppDispatch } from '../../redux/redux-store';
import Logo  from '../../img/Logo.jpg';


export type PropsType = {}
const { Header } = Layout;

/** Header of the site */
const AppHeader:React.FC<PropsType> = (props) => {

	const isAuth = useSelector(selectIsAuth);
	const login = useSelector(selectCurrentUserLogin);

	/** TYPE CAST for "UseDispatch" due to Redux changes*/
	const dispatch: AppDispatch = useDispatch()  	//const dispatch = useDispatch()

	/** logout*/
	const logoutCallback = ()=> {
		dispatch(logout())
	}

	return (
		<Header className={s.header}>
			<Row>
				<Col span={3}>
					<div className={s.logoBlock}>	
						<img className={s.demoLogo} src={Logo} alt=''/>
					</div>
				</Col>

				<Col span={15} className={s.logoText}>
					<div>social network</div>
				</Col>

				{isAuth ? <>
					
					<Col span={1} >
							<Avatar style={{ backgroundColor: '#87d068' }} icon={<UserOutlined />} />
					</Col>
					<Col span={3} className={s.nicknameText}>
							{login} 
					</Col>
					<Col span={2}  >
						<Button onClick={logoutCallback}>Log out</Button>
					</Col>
					</>
				: 	
				<>
					<Col span={4} >
					</Col>
					<Col span={2} >
						<Button>
							<NavLink to={'/login'}>Login</NavLink>
						</Button>
					</Col>
				</>
				}
			</Row>
		</Header> 
	);
}

export default AppHeader;