/** Changes "Status" on Profile page, using "local State" from Redux*/
import React, {useState, useEffect, ChangeEvent} from 'react';
import { EditOutlined} from '@ant-design/icons';


type PropsType = {  
	status: string
	updateStatus: (status: string)=>void
	isOwner:boolean
}

/** Changes "Status" on Profile page, using "local State" from Redux*/
const ProfileStatusWithHooks: React.FC<PropsType> = (props) => {
	
	//В [0] сидит editMode, в [1] ф-ция, меняющая editMode
	let [editMode, setEditMode] = useState(false); //Дает LockalState в "editMode"  и ф-цию, которая его меняет "setEditMode"
	let [status, setStatus] = useState(props.status);

	useEffect (() =>{
		setStatus(props.status);
		}, [props.status]
	);

	const activateEditMode = () =>{
		if (props.isOwner) setEditMode(true);
	}

	const deactivateEditMode = () =>{
		setEditMode(false);

		/** send status to server */
		props.updateStatus(status);
		}
	

	/** Changes "Status" on Profile page, using "local State" from Redux*/	
	const onStatusChange = (e: ChangeEvent<HTMLInputElement>) =>{
		/** "(e: ChangeEvent<HTMLInputElement>)" - standard from react*/
		setStatus(e.currentTarget.value);
		}
	
		return (
			<div> 
				{!editMode &&
					<div>
						<b>Status : </b>
						<span onDoubleClick={activateEditMode}>{props.status || "----------"} &nbsp; </span>
						<EditOutlined onClick={activateEditMode}/>

					</div>			
				}

				{editMode &&
					<div>
						<input  onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode} value={status}
						/>
					</div>			
				}
			</div>
		); 
}
export default ProfileStatusWithHooks;