/** Add a new Post in Profile Page*/
import React from 'react';
import { SubmitErrorHandler, SubmitHandler, useForm } from 'react-hook-form';
import s from './AddPostForm.module.css';


//(2. profileInfo "onSubmit (function "saveProfile" ("formData" from Redux))" (1. ProfileDataForm "Form")))

/** What data does the form collect (what the user enters)*/
export type AddPostFormType = {
	newPostText: string
}

type MapDispatchPropsType = {  
		addPost: (newPostText: string) => void
}


/**1. component with Form for adding a new post*/
export default function AddPostForm (props: MapDispatchPropsType){

	// const AddPostForm = ({handleSubmit, profile, error}) =>{
	// "useForm" Generik  What you can do with a form and Default form field values
	// {register - registration, handleSubmit - data collection in the form, formState- form state (errors here)
	const { register, handleSubmit, formState: { errors } } = useForm<AddPostFormType>({	})


//  Submit----------------------------------------------------------------
	//Successful Submit
	const submit : SubmitHandler<AddPostFormType > = (formData) =>{ 
		props.addPost(formData.newPostText);
	}

	//unsuccessful Submit
	const error : SubmitErrorHandler<AddPostFormType> = data =>{
	}

	return (
		<form onSubmit={handleSubmit(submit, error)}> 

				{/* {error && <div className={style.formSummaryError}>
					{error}
				</div>} */}

				{/* Fields of the form */}
				<div>
					<b>Add post:&nbsp; </b> 
					<input type='textarea' {...register('newPostText', 
													{maxLength:  {value: 520, message: 'Text of post exceeds 520 symbols'}})} />
					<div className={s.buttonSave}></div>																		
					<button >Save</button>																		
				</div>
		</form>
	)
}