/** Filters for User page */
import React from 'react';
import { SubmitErrorHandler, SubmitHandler, useForm } from 'react-hook-form';
import { FilterType } from '../../redux/users-reducer';
import { useSelector } from 'react-redux';
import { getUsersFilter } from '../../redux/users-selectors';

/** (2. UserSearchForm "onSubmit" (function "requestUsers (formData from Redux))" (1. UserSearchForm "Form"))) */


/** FilterType - What data does the form collect (what the user enters)*/

/** What do we pass to the function with the form (and we pass the handler function)*/
type UserSearchFormPropsType = {  
	onFilterChanged : (filter: FilterType) => void
}


type FriendFormType =   
	"true" | "false" | "null"


/** What types will be in "Submit"-е (not FilterType)*/
type FormType = {  
	term : string,
	friend: FriendFormType
}


/**  1. Main component with form*/

const UserSearchForm: React.FC <UserSearchFormPropsType> = (props) => {

/** We get the previously entered value of "filter" and then it is in "initialState"*/
const filter = useSelector(getUsersFilter)


	/** "useForm" Generik */
	const { register, handleSubmit, formState: { errors } } = useForm<FormType>({
		defaultValues: {              
			term: filter.term,
			friend: String(filter.friend) as FriendFormType
		}
	})

	const submit : SubmitHandler<FormType > = (formData) =>{ //Ф-ция обработки поиска User-а
		const filter:FilterType = {
			term: formData.term,
			friend: formData.friend === 'null' ? null    :     formData.friend === 'true' ? true     :    false
		}
		props.onFilterChanged(filter);
	}
	
	const error : SubmitErrorHandler<FilterType> = data =>{
	}


	return (
		<form onSubmit={handleSubmit(submit, error)}> 


				{/* Покажем ошибку с указанием в каком поле она сделана */}
				{/* {error && <div className={style.formSummaryError}>
					{error}
				</div>} */}
				<div>
					
				</div>


				{/* Form fields */}
				<div>
					<b>User Search</b> : <input type='input' {...register('term', 
																							{maxLength:  {value: 20, message: ' exceeds 20 symbols'}})} />
					<div style={{padding: "10px", display:"inline-block"}}> 
						{/* select field---------------------------------------------*/}
						<select {...register("friend")}>
						<option value="null">All</option>
						<option value="true">Only followed</option>
						<option value="false">Only unfollowed</option>
						</select>
					</div>																		
					
				
					<button>Search</button>																		
				</div>
		</form>
	)
}

export default UserSearchForm