import React from 'react'
import s from "./Login.module.css";
import { SubmitErrorHandler, SubmitHandler, useForm } from 'react-hook-form';
import { login } from "../../redux/auth-reducer";
import { Navigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, AppStateType } from '../../redux/redux-store';


// Structure : (2 LoginPage (1 LoginForm))
// 2 LoginPage provides (1 LoginForm)) with: 	"captchaUrl"


// 1. LoginForm ----------------------------------------------------------------------------------
/**
 * 1. LoginForm collects user data for Login
 */
function LoginForm (props: LoginFormPropsType) {

	/** "useForm" - Generik  What you can do with a form and Default form field values
	 *{register - registration, handleSubmit - data collection in the form, formState- form state (errors here)}-Form handling capabilities
	 */
	const { register, handleSubmit, formState: {errors}  } = useForm<ILoginForm>({
      defaultValues: {              //to pass additional parameters
			captcha : "",
			email : "",
			password : "",
      }
   })
//  Submit----------------------------------------------------------------
	/** Successful Submit */
	const submit : SubmitHandler<ILoginForm > = (formData) =>{
		props.loginSubmit(formData);
	}

	/** Unsuccessful Submit */
	const error : SubmitErrorHandler<ILoginForm > = data =>{
	}
//----------------------------------------------------------------

	return (
		<>
		{/* a worker of the form; at the entrance successful and unsuccessful Submit*/}
			<form onSubmit={handleSubmit(submit, error)}>   
				<div className={s.loginInput}>
					<input type='text' {...register('email', {required: "Email Address is required", 
																			pattern: {
																				value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/,
																				message: 'Incorrect Email' 
																			}})} 
						className={errors.email? s.loginFormError :""} aria-invalid={errors.email? true :false} placeholder="email"/>
						{ errors.email && 
							<span className={s.loginFormTextError}>{ errors.email?.message }</span>
						}
				</div>
				
				<div className={s.loginInput}>
					<input type='password' {...register('password', {required: "Password is required", 
																					maxLength:  {value: 20, message: 'Password exceeds 20 symbols'},
																					minLength:  {value: 4, message: 'Password shorter 4 symbols'}
																					})} 
																					className={errors.email? s.loginFormError :""} 
																					placeholder="Password"/>
					{ errors.password && 
							<span className={s.loginFormTextError}>{ errors.password?.message }</span>
						}
				</div>
				

				<div >
					<input type='checkbox' {...register('rememberMe')} className={s.loginCheckbox}/>
					remember me
				</div>

				{/* Captcha--------------------------------------------- */}
				{props.captchaUrl && <img src = {props.captchaUrl} alt="captcha"/>}
				{props.captchaUrl && <input type='input' {...register('captcha', {required: "Captcha is required"} )} placeholder="Symbol from image"/>}
				{/* --------------------------------------------- */}

				
				<div className={s.loginButton}>
				<button className={errors.email || errors.password ? s.loginButtonInaccessible :""}>Login</button>
				</div>
			</form> 
		</>
	)
}

/** What data does the form collect (what the user enters) */
interface ILoginForm {
	captcha : string
	email : string
	password : string
	rememberMe : boolean | null
}

/** What  the component "LoginForm" receives */
type LoginFormPropsType = {
	captchaUrl : string | null,
	loginSubmit: (formData: ILoginForm) =>void
}


//-----------------------------------------------------------------------------------------
/**
 * 2. LoginPage (LoginForm))
 * LoginPage provides LoginForm with: 	"captchaUrl"
 */
export const LoginPage : React.FC = () => {
	
	// "useSelector( our selector)" to obtain data from "state" 
	const captchaUrl = useSelector((state: AppStateType)=>state.auth.captchaUrl)
	const isAuth = useSelector((state: AppStateType)=>state.auth.isAuth)

	//const dispatch = useDispatch()  to obtain "dispatch"
	const dispatch: AppDispatch  =  useDispatch()
	const loginSubmit = (formData: ILoginForm) =>{
		dispatch(login(formData.email, formData.password, formData.rememberMe ?? false, formData.captcha));
	}


	if (isAuth) {
		//"Navigate" instead of "Redirect"
		return <Navigate to={("/Profile")} />
	}

	return (
		<div className={s.wrapper}>
			<h1>Login </h1>
			<div>Use demo account to login:</div>
			<div>Email: free@samuraijs.com</div>
			<div style={{margin: "0 0 20px 0" }}>Password: free</div>
			<LoginForm captchaUrl={captchaUrl}  loginSubmit = {loginSubmit}/>
		</div>
	)
}

export default LoginPage





