/** Display list of users for User page*/
import React from 'react';
// import styles from './Users.module.css';
import userPhoto from '../../img/UserIcon.png';
import {NavLink} from  'react-router-dom';
import { UserType } from '../../types/types';
import s from './User.module.css';


type PropsType = {
	user: UserType
	followingInProgress:  Array<number>
	follow : (userId: number) => void
	unfollow : (userId: number) => void
}

/** Display list of users for User page*/
let User: React.FC<PropsType> = ({user, followingInProgress, follow, unfollow})=>{
return (
	
		<div className={s.userCard}>
			<NavLink to = {'/profile/' +user.id}>
			{/* userPhoto - default photo */}
				<img src={user.photos.small !=null ? user.photos.small : userPhoto} className={s.userPhoto} alt="" />
				<div className={s.userCardName}>{user.name}</div>
			</NavLink>

			<div className={s.userCardText}>
				<div className={s.userCardStatus}>
					<b>Status:</b>&nbsp;{user.status}
				</div>
				<div className={s.userFollow}>
					{user.followed 
					
					? <button disabled={followingInProgress.some(id => id === user.id)} 
									onClick={()=>{unfollow(user.id);
					}}>


					unfollow</button> 
					: <button disabled={followingInProgress.some(id => id === user.id)} onClick={()=>{follow(user.id);
						}}>
						follow</button>
					}
				</div>
			</div>			
		</div>
	
	)
};

export default User;
