/** Display and edit user profile*/
import React, { useState } from 'react';
import s from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';
import ProfileDataForm from './ProfileDataForm';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import userPhoto from '../../../assets/img/UserUnknown.png';
import { ProfileType } from '../../../types/types';



/** Main component ProfileInfo(ProfileData(Contact))*/
const ProfileInfo : React.FC<ProfileInfoPropsType>= ({ profile, status, updateStatus, isOwner, savePhoto, saveProfile }) => {

	/** Gives "editMode" and the function that changes it "setEditMode"*/
	let [editMode, setEditMode] = useState(false);

	if (!profile) {
		return <Preloader />
	}

	const onMainPhotoSelected = (e: React.ChangeEvent<HTMLInputElement>) => {
		if (e.target.files?.length) {			//Если файл есть, то берем его длину
			savePhoto(e.target.files[0]);
		}
	}

	// "then" function only to enable "EditMode" 
	//only when everything was saved to the server, i.e. there were no errors. (remove then)
	//to this in "profile-reducer.js" you need to add "Promise.reject (response.data.messages[0])"; 
	const onSubmit = (formData: ProfileType) => {
		saveProfile(formData).then(
			() => { setEditMode(false); }
		);
	}

	return (
		<div>
			
			<div className={s.img1}>
				<img src='https://img.freepik.com/free-photo/tropical-beach_74190-188.jpg' alt='' />
			</div>

			<div>
			<div className={s.mainNameBlock}>
				<div className={s.mainPhotoBlock1}>
					<img src={profile.photos.large || userPhoto} className={s.mainPhoto} alt='' />
					<div className={s.mainPhotoBlock2}>
						<div className={s.mainPhotoBlock2Name}>{profile.fullName}</div>
						<div className={s.profileStatus}>
							<ProfileStatusWithHooks status={status} updateStatus={updateStatus} isOwner={isOwner}/>
						</div>

						{/* If you are the owner of this profile, then show the button to select a photo  */}
						<div className={s.mainPhotoButton}>
							{isOwner && <input type={"file"} onChange={onMainPhotoSelected} />}
						</div>
					</div>
				</div>
			</div>
				
				<div className={s.profileInfoForm}>
					{/* If "Edit Mode", then show the form for changing the profile */}
					{editMode ?
						<ProfileDataForm initialValues={profile} saveProfile={onSubmit} /> 
						:
						<ProfileData goToEditMode={() => { setEditMode(true) }} profile={profile} isOwner={isOwner} />}
				</div>
			</div>
		</div>
	);
}

type ProfileInfoPropsType = {  
	profile : ProfileType  | null
	status : string
	updateStatus : (status : string)=>void
	isOwner : boolean
	savePhoto : (file : File)=>void
	saveProfile : (formData:ProfileType)=>Promise<any>
}


/** Displays Profile's information*/
const ProfileData : React.FC<ProfileDataPropsType> = ({ profile, isOwner, goToEditMode }) => {
	return <div>
		<div className={s.editProfileButton}>	
			{isOwner && <div><button onClick={goToEditMode}>Edit Profile</button>
		</div>}
		</div>
		<div className={s.profileSection}>
			<b>Full name</b> : {profile.fullName}
		</div>
		<div className={s.profileSection}>
			<b>Looking for a job</b> : {profile.lookingForAJob ? "yes" : "no"}
		</div>
		<div className={s.profileSection}>
			<b>My professional skills</b> : {profile.lookingForAJobDescription}
		</div>
		<div className={s.profileSection}>
			<b>About me</b> : {profile.aboutMe}
		</div>
		
		<div className={s.profileSection}>
			<b>Contacts:</b>
			<Contact key={1} contactTitle="GitHub" contactValue={profile.contacts.github} />
			<Contact key={2} contactTitle="Facebook" contactValue={profile.contacts.facebook} />
			<Contact key={3} contactTitle="Instagram" contactValue={profile.contacts.instagram} />
			<Contact key={4} contactTitle="vk" contactValue={profile.contacts.vk} />
			<Contact key={5} contactTitle="X (twitter)" contactValue={profile.contacts.twitter} />
			<Contact key={6} contactTitle="website" contactValue={profile.contacts.website} />
			<Contact key={7} contactTitle="youtube" contactValue={profile.contacts.youtube} />
			<Contact key={8} contactTitle="mainLink" contactValue={profile.contacts.mainLink} />
		</div>
	</div>
}

type ProfileDataPropsType = {
	profile: ProfileType
	isOwner: boolean
	goToEditMode: ()=>void
}


/** Displays a part of Profile's information, namely "Contacts"*/
const Contact  : React.FC<ContactPropsType>= ({ contactTitle, contactValue }) => {
	return <div className={s.contact}><b>{contactTitle}</b>: {contactValue}</div>
}

type ContactPropsType = {
	contactTitle: string
	contactValue: string
}

export default ProfileInfo;